.hero-container{
    width: 100%;
    height: auto;
  
}

#video{
    width: 100%;
    height: 100%;
}

.hero-text{
    position: absolute;
    top: 50%;
    left: 20%;
    right: 20%;
    display: flex;
    flex-wrap: wrap;
}
@media(max-width:900px){
    .hero-text{
        position: absolute;
        top: 25%;
        
    }
}
@media(max-width:475px){
    .hero-text{
        position: absolute;
        top: 18%;
        
    }
}


.hero-text p{
    color: white;
    font-size: 3vw;
    text-align: center;
    font-weight: 500;
    display: flex;
    flex-wrap: wrap;
}
.shop-btn{
   position: absolute;
   top: 65%;
   left: 46%;
   right: 45%;
   
}
.btn{
    padding: 12px 32px ;
    font-size: 0.7rem;
    background-color: hsla(0, 84%, 48%, 0.834);
    color: white;
    border: none;
}
@media (max-width:900px){
    .shop-btn{
        position: absolute;
        top: 33%;
        left: 40%;
        right: 40%;
        
     }

}
@media (max-width:475px){
    .shop-btn{
        position: absolute;
        top: 23%;
        left: 31%;
        right: 31%;
        
     }

}