.about{
    height: auto;
    width: 100%;
    background-color: rgb(240, 229, 229);
    display: flex;
    flex-wrap: wrap;

}
.about-container{
   height: 75%;
    width: 70%;
    margin: auto;
    /* background-color: yellow; */
    /* display: flex;
    flex-wrap: wrap; */
    margin-top: 3rem;
    margin-bottom: 3rem;


}



.right-float{

  /* background-color: blue; */
  width: 50%;
  height: 100%;
  float: right;

}
@media(max-width:900px){
    .right-float{
        width: 95%;
        margin: auto;
    }
}
.img{
    width: 100%;
    height: 100%;
    margin: auto;
    border-radius: 10px 70px 10px;
    /* float: right; */
   
}
.left-float{
    height: auto;
    width: 50%;
    /* background-color: red; */
    float: left;
    margin: auto;
 
}
@media(max-width:900px){
    .left-float{
        width: 95%;
        margin: auto;
    }
}
.left-float p{
    /* text-align: center; */
    font-size: 1rem;
    
  
}
.about-text{
    text-align: center;
    font-size: 1.5rem;
    margin-bottom: 1rem;
}