.whyfab-container{
    height: auto;
    width: 100%;
    background-color: white;
    
   

}

.sub-container{
    width: 80%;
    height: 80%;
    border-radius: 20px;
    margin-top: 6%;
    margin-bottom: 5%;
    margin: auto;
    
    
  
  
}
@media(max-width:900px){
    .sub-container{
        width: 95%;
        height: auto;
    }

}
.sub-container h4{
    text-align: center;
    color: black;
    font-size: 3vw;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2rem;
}
.boxes{
    display: flex;
    gap: 0.1rem;
    display: flex;
    flex-wrap: wrap;
    margin-top: 2rem;
    margin-left: 15%;
    margin-right: 15%;
    margin-bottom: 5rem;
  
    
}
@media(max-width:900px){
    .boxes{
        margin-left: 1%;
        margin-right: 1%;
    
    }
}
.box{
    height: 170px;
    width: 270px;
    background-color: hsla(0, 68%, 35%, 0.834);
    color: white;
    padding: 1.5rem;
    margin: auto;
    margin-bottom: 1rem;

   
}
@media(max-width:900px){
    .box{
        height: 170px;
        width: 230px;
 

    }
}


